import { ConnectorRuntime } from '@transai/connector-runtime';
import { BaseConnectorConfig } from '@xip-online-data/types';

import { DummyDataGeneratorService } from './dummy-data-generator.service';

export class ConnectorRunnerDummySource extends ConnectorRuntime<
  NonNullable<unknown>
> {
  readonly CONNECTOR_INSTANCE: string = 'XOD_CONNECTOR_DUMMY_SINK_CONFIG';

  #generator: DummyDataGeneratorService | undefined;

  override getConfig = async (): Promise<BaseConnectorConfig> => ({
    processIdentifier: this.CONNECTOR_IDENTIFIER,
    tenantIdentifier: 'dummy-tenant',
    datasourceIdentifier: 'dummy-source',
    kafka: {
      brokers: ['localhost:9092'],
      groupId: 'dummy-source',
      clientId: 'dummy-source',
    },
  });

  override init = async (): Promise<void> => {
    this.#generator = new DummyDataGeneratorService(
      this.kafkaService,
      this.config,
      this.offsetStore,
    );
  };

  override exit = async () => {
    this.#generator?.stop();
  };
}
